body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

p {
  text-indent: 2em; }

body {
  font-family: sans-serif !important;
  font-weight: 600 !important;
  font-size: 14px !important;
  text-shadow: 0px 0px 3px rgba(0, 0, 0, 0.5) !important; }

.article {
  text-indent: 2em; }
  @media (max-width: 992px) {
    .article {
      display: -webkit-flex;
      display: flex;
      -webkit-flex-direction: column-reverse !important;
              flex-direction: column-reverse !important; }
      .article .text {
        min-width: 100% !important; }
      .article .img {
        min-width: 100% !important; } }

.sideBarShow {
  transition: -webkit-transform 0.36s;
  transition: transform 0.36s;
  transition: transform 0.36s, -webkit-transform 0.36s;
  -webkit-transform: translate(0%, 0%);
          transform: translate(0%, 0%);
  box-shadow: -1000px 0px 0px 1000px rgba(0, 0, 0, 0.6) !important; }

.sideBarHidden {
  transition: -webkit-transform 0.36s;
  transition: transform 0.36s;
  transition: transform 0.36s, -webkit-transform 0.36s;
  -webkit-transform: translate(100%, 0%);
          transform: translate(100%, 0%); }

.menuButton:hover {
  color: red; }

.footer {
  margin-top: 100px !important; }

.segmentArt {
  -webkit-transform: translate(-66%, 0%);
          transform: translate(-66%, 0%);
  opacity: 0;
  transition: opacity 0.7s ease, -webkit-transform 0.7s ease;
  transition: transform 0.7s ease, opacity 0.7s ease;
  transition: transform 0.7s ease, opacity 0.7s ease, -webkit-transform 0.7s ease; }

.segmentArtIntersect {
  transition: opacity 0.7s ease , -webkit-transform 0.7s ease;
  transition: transform 0.7s ease , opacity 0.7s ease;
  transition: transform 0.7s ease , opacity 0.7s ease , -webkit-transform 0.7s ease;
  opacity: 1;
  -webkit-transform: translate(0%, 0%);
          transform: translate(0%, 0%); }

