$imp: !important;

p{
    text-indent: 2em;
}

body{
    font-family:sans-serif $imp;
    font-weight:600 $imp;
    font-size: 14px $imp;
    text-shadow:0px 0px 3px rgba(0,0,0,0.5) $imp;
}

.article{
    text-indent: 2em;
    @media (max-width: 992px){
        display: flex;
        flex-direction: column-reverse $imp;
        .text{
            min-width: 100% $imp;
            
        }
        .img{
            min-width: 100% $imp;
           //display:none $imp;
        }
    }
}


.sideBarShow{
    transition: transform 0.36s;
    transform: translate(0%,0%);
    
    box-shadow: -1000px 0px 0px 1000px rgba(0,0,0,0.6) $imp;
}
.sideBarHidden{
    transition: transform 0.36s;
    transform: translate(100%,0%);
    
}

.menuButton{
    &:hover{
        color:red;
    }
}

.footer{
    margin-top: 100px $imp;
}


.segmentArt{
        transform: translate(-66%,0%);
        opacity: 0;
        transition: transform 0.7s ease, opacity 0.7s ease;
}

.segmentArtIntersect{
        transition: transform 0.7s ease , opacity 0.7s ease;
        opacity: 1;
        transform: translate(0%,0%);
       
}
